<template>
	<div class="img">
		<img :src="imgSrl" alt="">
		<img @click="back_index()" src="../images/close.png" alt="">
	</div>
</template>
<script>
export default {
  data () {
    return {
    	imgSrl: null
    }
  },
  methods: {
  	init() {
  		this.imgSrl= this.$route.query.imgUrl
  	},
    back_index() {
    	this.$router.push({path: '/index'})
    }
  },
  mounted() {
  	this.init()
  }
}
</script>

<style lang="scss" rel="stylesheet/scss">
	.img{
		z-index: 100;
		width: 100%;
		height: 100%;
		position: fixed;
		left: 0;
		right: 0;
		top: 0;
		bottom: 0;

		img:nth-child(1){
			width: 100%;
			height: 100%;
			vertical-align: bottom;
		}
		img:nth-child(2){
			width: .32rem;
			position: absolute;
			top: 0.4rem;
			right: 0.4rem;
		}
	}
</style>



